input,select{
  width: 100%;
}
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
}

form {
  max-width: 900px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: white;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 15px;
}

.form-group label {
  flex: 1 0 20%;
  padding: 10px;
  font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="date"],
.form-group input[type="number"],
.form-group input[type="time"],
.form-group select {
  flex: 1 0 75%;
  padding: 10px;
  margin: 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.form-group input[type="checkbox"] {
  margin-right: 10px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="date"]:focus,
input[type="number"]:focus,
input[type="time"]:focus,
select:focus {
  border-color: #007bff;
  outline: none;
}

.error {
  color: red;
  font-size: 0.85em;
  margin-top: 5px;
  display: block;
}

button[type="submit"] {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

@media (min-width: 600px) {
  .form-group {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
  }

  .form-group label {
      flex: 1 0 20%;
  }

  .form-group input[type="text"],
  .form-group input[type="email"],
  .form-group input[type="date"],
  .form-group input[type="number"],
  .form-group input[type="time"],
  .form-group select {
      flex: 1 0 75%;
  }
}